import api from '../../axios';

//  
export function getFilters(id) {
    return api.get('/houses/condition',{
        params:{
            id
        }
    })
}
// 
export function getListByFilter(cityId, filters, start, end) {
    return api.get('/houses', {
        params: {
            cityId,
            // 
            ...filters,
            // 
            start,
            end
        }
    })
}







