import React from 'react'

import { Flex } from 'antd-mobile'

import Filter from './components/Filter'
// 
import styles from './index.module.css'
import { getListByFilter } from '../../utils/api/House'
import { getCurCity } from '../../utils/quanju'

import { List, AutoSizer } from 'react-virtualized';
import HouseItem from '../../components/HouseItem'
import { BASE_URL } from '../../utils/axios'

export default class HouseList extends React.Component {

  // state
  // state  => html => idff DOMjsx+state
  state = {
    // 
    list: []
  }

  // 
  renderHouseItem = ({
    key,
    index,
    isScrolling,
    isVisible,
    style,
  }) => {
    //  => 
    const { list } = this.state;
    // 
    const item = list[index];
    // 
    item.src = `${BASE_URL}${item.houseImg}`
    return (
      <HouseItem {...item} key={key} style={style} />
    )
  }

  async componentDidMount() {
    let { value } = await getCurCity();
    this.cityId = value;
    this.getHouseList()
  }

  // 
  // 
  onFilter = (filters) => {
    console.log('', filters);
    //  => this
    this.filters = filters;
    // 
    // 
    this.getHouseList()
  }

  // 
  getHouseList = async () => {
    let { status, data: { list } } = await getListByFilter(this.cityId, this.filters, 1, 20)
    if (status === 200) {
      this.setState({
        list
      })
    }
  }

  render() {
    return (
      <div className={styles.root}>
        {/*  */}
        <Filter onFilter={this.onFilter} />
        {/*  */}
        {/*  */}
        <AutoSizer>
          {({ height, width }) => (
            <List 
              height={height}
              className={styles.houseList}
              rowCount={this.state.list.length}
              rowHeight={130}
              rowRenderer={this.renderHouseItem}
              width={width}
            />
          )}
        </AutoSizer>
      </div>
    )
  }
}
